import React, { useContext } from 'react';
import { Typography } from '@material-ui/core';
import { useModal } from '../../hooks'
import { PortfolioContext } from '../../helpers'
import Modal from './Modal'

export function Grid({ sectionTitle, gridElements = [], gridCssClass = "" }) {
    const modalViewMore = useModal()
    const { language, labels } = useContext(PortfolioContext);
    const { misc: { buttonViewMore } } = labels || {}

    return (
        <div className={"grid-container " + gridCssClass}>
            <GridModal open={modalViewMore.open} onClose={() => modalViewMore.onClose()} titulo={sectionTitle} isFullScreen={true} element={modalViewMore.element} labels={labels} />
            <Typography variant='h1' align="center" >{sectionTitle}</Typography>
            <div className="grid">
                {
                    gridElements.map((element, index) => (
                        <div className={"grid-element-container"} key={index} >
                            <div className='grid-element-img-container'>
                                <img src={element.img} alt="logo" />
                            </div>
                            <div className="grid-element-text-container">
                                <div className="top-text">
                                    <Typography variant='h3' align='center' >{element.titulo[language].toUpperCase()}</Typography>
                                    <Typography variant='h6' align='center' >{element.subtitulo[language].toUpperCase()}</Typography>
                                </div>
                                <button onClick={(event) => modalViewMore.onOpen(element)}><Typography variant='body1' align='center' >{buttonViewMore[language].toUpperCase()}</Typography></button>
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    );
}

function GridModal({ open, onClose, titulo, isFullScreen, element }) {
    const { language, labels } = useContext(PortfolioContext);
    const { misc: { linkLabels, linkIcons } } = labels
    const { img, titulo: elementTitle , subtitulo , descripcion, hashtags, links = [], time } = element || {}
   
    return (
        <Modal open={open} onClose={onClose} titulo={titulo} isFullScreen={isFullScreen}>
           { element ? <div className="grid-element-modal-content-container scrollable">
                <div className='grid-element-modal-img-container'>
                    <img src={img} alt="logo" />
                </div>
                <div className="grid-element-modal-text-container">
                    <Typography variant='h2'  >{elementTitle[language].toUpperCase()}</Typography>
                    <Typography variant='h4'  >{subtitulo[language].toUpperCase()}</Typography>
                    <Typography variant='body1'  >{hashtags[language].join(', ').toUpperCase()}</Typography>
                    <Typography variant='body1'  >{descripcion[language]}</Typography>
                   {time && <Typography variant='body1'  >{time[language]}</Typography>}
                </div>
                <div className="grid-element-modal-links-container">
                    {
                        links.map((linkElement, index) => {
                            const { link = "", icon = null, type = "" } = linkElement
                            return <div className="link" key={'lnik-icon' + index}>
                                <a href={link} target="_blank" rel="noopener noreferrer" >{linkIcons[type] || ""} <Typography variant='body1'  >{linkLabels[type][language] || ""}</Typography></a>
                            </div>
                        })
                    }
                </div>
            </div> : null}
        </Modal>
    )

}