import React, { useContext, useRef } from 'react';
import { Typography, Hidden } from '@material-ui/core';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import { ContactBar, ProjectsGrid, ArticulosGrid } from '../components'
import { Topbar, Footer } from '../components/Layout'
import { PortfolioContext } from '../helpers'

export function Home({ setLanguage }) {
  const { language, labels } = useContext(PortfolioContext);
  const { header: { titleDescription, titleDescriptionTwo, about, sectionTitles } } = labels || {}

  const myRef = useRef(null)
  const onArrowDownClick = () => {
    console.log("scroll", myRef.current)
    myRef.current.scrollIntoView({ behavior: 'smooth', block: 'start' })

    // window.scrollTo(0, myRef.current.offsetTop)
  }

  return (
    <div className="home" >
      <Topbar setLanguage={setLanguage} />
      <div className="home-parallax scrollable">
        <div className='home-background-img'>
          <img src={require('../assets/background-imgs/black-wireless-computer-keyboard-beside-black-ceramic-mug-3740725.jpg')} alt="logo" />
        </div>
        <div className='home-background-overlay' />
        <div className="home-title-container">
          <Hidden xsDown><Typography variant='h1' align="center">LEANDRO ERCOLI</Typography></Hidden>
          <Hidden smUp><Typography variant='h2' align="center">LEANDRO ERCOLI</Typography></Hidden>
          <div className='animated-separator' />
          <div className='specialities'>
            <Typography variant='h6' align="center" className='home-subtitle'>{titleDescription[language].toUpperCase()}</Typography>
            <Typography variant='h6' align="center" className='home-subtitle'>{titleDescriptionTwo[language].toUpperCase()}</Typography>
          </div>
        </div>
        <div className="home-arrow-container">
          <div className="arrow" onClick={onArrowDownClick} >
            <KeyboardArrowDownIcon fontSize='large' />
          </div>
        </div>
        <div className="home-profile-avatar" >
          <img src={require('../assets/background-imgs/IMG-20170409-WA0023-round.jpg')} className="img-fit-landscape" alt="logo" />
        </div>
        <div className="home-profile-about-me" ref={myRef}>
          {
            about[language].map((about, index) =>
              <Typography variant='h6' align='left' key={index}>{about}</Typography>
            )
          }
        </div>
        <div className="home-profile-contact">
          <Typography variant='h1'>{sectionTitles.contact[language]}</Typography>
          <ContactBar />
        </div>
        <div className="home-content">
          <ProjectsGrid />
          <ArticulosGrid />
          <Footer />
        </div>
      </div>
    </div>
  );
}
